body {
  background-color: green;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.result {
  text-align: center;
  color: gold;
  font-family: Libre Baskerville, serif;
  font-size: 100px;
}

#key {
  text-align: center;
  color: gold;
  font-family: Libre Baskerville, serif;
  font-size: 80px;
}

.game-button {
  color: green;
  background-color: gold;
  border: 3px solid #adff2f;
  border-radius: 10px;
  margin: 0 auto;
  padding: 10px;
  font-size: 60px;
  animation-name: btn-move;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  display: flex;
  box-shadow: 0 2px 6px #f4c43059, 0 4px 12px #00000014;
}

@keyframes btn-move {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
